"use client"; // Add this line at the top of the file

import { useState } from "react";
import { FaUser, FaLock } from "react-icons/fa";
import axios from "axios"; // Import axios

const Login = () => {
  // State variables to hold the username, password, and error messages
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Clear any previous errors
    setError("");

    // Prepare the data to be sent to the backend
    const data = {
      username,
      password,
    };

    try {
      // Make POST request to the backend (Node.js server running on port 3002)
      const response = await axios.post("http://localhost:3002/login", data);

      // Handle success - assuming the backend responds with a success message or token
      if (response.status === 200) {
        // Do something with the response (e.g., store token, redirect, etc.)
        console.log("Login successful", response.data);
        // Redirect to a different page if needed (e.g., home page)
        window.location.href = "/dashboard"; // example redirection
      }
    } catch (err: any) {
      // Handle errors (e.g., incorrect username/password)
      console.error(err);
      setError("Invalid username or password");
    }
  };

  return (
    <div
      className="flex items-center justify-center min-h-screen px-4 py-8 sm:py-16"
      style={{
        backgroundImage: "url('/2.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
      }}
    >
      <div className="bg-black bg-opacity-60 p-6 sm:p-8 rounded-lg shadow-lg w-full max-w-full sm:max-w-xs md:max-w-sm lg:max-w-md">
        <h2 className="text-white text-xl sm:text-2xl font-semibold text-center mb-6">
          Login
        </h2>
        {error && (
          <div className="text-red-500 text-center mb-4">{error}</div>
        )}
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="flex items-center bg-gray-200 p-2 sm:p-3 rounded-lg">
            <FaUser className="text-gray-600 mr-2 sm:mr-3" />
            <input
              type="text"
              placeholder="UserName"
              className="bg-transparent flex-1 outline-none text-gray-700 text-sm sm:text-base"
              value={username}
              onChange={(e) => setUsername(e.target.value)} // Update username on input change
            />
          </div>
          <div className="flex items-center bg-gray-200 p-2 sm:p-3 rounded-lg">
            <FaLock className="text-gray-600 mr-2 sm:mr-3" />
            <input
              type="password"
              placeholder="Password"
              className="bg-transparent flex-1 outline-none text-gray-700 text-sm sm:text-base"
              value={password}
              onChange={(e) => setPassword(e.target.value)} // Update password on input change
            />
          </div>
          <button
            type="submit"
            className="w-full bg-gray-300 text-gray-900 font-semibold py-2 sm:py-3 rounded-lg hover:bg-gray-400 transition duration-300"
          >
            Login
          </button>
          <p className="text-center text-white mt-2 text-xs sm:text-sm cursor-pointer hover:underline">
            Forgot Password?
          </p>
        </form>
      </div>
    </div>
  );
};

export default Login;
